import React, { FC } from 'react'
import cl from './Message.module.scss';

interface Idata {
    message: string;
}

interface MessageProps {
    data: Idata;
    isOutgoing: boolean;
}

const Message: FC<MessageProps> = ({
    data,
    isOutgoing
}) => {

    return (
        <div className={`${cl.main_wrapper} ${isOutgoing ? cl.outgoing : cl.incoming}`}>
            <div className={`${cl.content_wrapper}`}>
                <div className={cl.content}>
                    {!isOutgoing &&
                        <span className={cl.username}>
                            Alanominator
                        </span>
                    }
                    <div className={cl.message_text}>
                        {data.message}

                        <span className={cl.time}>
                            12:13
                        </span>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Message;