import React, { FC, useEffect, useRef } from 'react'
import cl from './MessageForm.module.scss'
import debounce from "lodash/debounce";

interface MessageFormProps {
    message: string;
    setMessage: any;
    onSubmit: any;
    setIsUserTyping: any;
}

const MessageForm: FC<MessageFormProps> = ({
    message,
    setMessage,
    onSubmit,
    setIsUserTyping,
}) => {

    const inputElement = useRef<HTMLTextAreaElement>(null)

    const handleIsTyping = debounce(function () {
        // continually delays setting "isTyping" to false for 500ms until the user has stopped typing and the delay runs out
        setIsUserTyping(false);
    }, 850);

    const onInputChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
        e.preventDefault()

        setMessage(e.target.value)

        setIsUserTyping(true)

        handleIsTyping()

        console.log(message)
    }

    const onSubmitForm = (e: any) => {
        console.log("onSubmitForm")

        onSubmit(e)

        if (inputElement.current) inputElement.current.focus()
    }

    useEffect(() => {
        const keyDownEventListenerFunction = (e: any) => {
            if (e.ctrlKey && e.code === 'Enter') {
                onSubmitForm(e)
                console.log("Ctrl + Enter")
            }
            if (e.code === 'Enter' && !e.ctrlKey) {
                console.log("new string")
            }
        }

        window.addEventListener('keydown', keyDownEventListenerFunction)

        return () => {
            window.removeEventListener('keydown', keyDownEventListenerFunction, false)
        }
    }, [])

    return (
        <form className={cl.formWrapper} onSubmit={onSubmitForm}>

            <div className={cl.message_input_wrapper}>

                <textarea
                    placeholder='Message'

                    value={message}

                    ref={inputElement}

                    className={cl.message_input}

                    onChange={onInputChange}
                />
            </div>

            {message ?
                <button className={cl.button}>
                    Send
                </button>
                :
                <div
                    className={cl.openSendFile}
                >
                    file
                </div>
            }

        </form>
    )
}

export default MessageForm