import React, { FC } from 'react';
import cl from './ConnectedOrNotMessage.module.scss';

interface PropTypes {
    connected: Boolean;
}

const ConnectedOrNotMessage: FC<PropTypes> = ({ connected }) => {

    return (
        <>
            {connected === false ?
                <div className={cl.notConncetedMessageWrapper}>
                    <div className={cl.notConncetedMessageContent}>
                        <span className={cl.notConncetedMessageText}>
                            Conncetion failed. Retrying
                        </span>
                        <span className={cl.notConncetedMessageSpinnerWrapper}>
                            <span className={cl.notConncetedMessageSpinner} />
                        </span>
                    </div>
                </div>
                : connected === true ?
                    <span className={cl.conncetedMessage} />
                    :
                    <>
                    </>
            }
        </>
    )
}

export default ConnectedOrNotMessage;