import React, { useState } from 'react'
import * as Yup from 'yup';
import { Formik, Form } from 'formik'
import { useFetching } from '../../../hooks/useFetching';
import axios from 'axios';
import { basic_url } from '../../../API/APISettings';
import FormButton from '../../../components/UI/FormButton/FormButton';
import FormInput from '../../../components/UI/FormInput/FormInput';
import { useNavigate } from 'react-router-dom';
import { store } from '../../../reducers/index';
import { LOGIN_SUCCESS, LOGIN_FAIL } from '../../../reducers/auth';
import { isEmpty } from '../funcs';
import cl from '../authPages.module.scss';
import t from './ActivateUserPage.module.scss';
import Loader from '../../../components/UI/Loader/Loader';
import { useTranslation } from 'react-i18next';
import { useAlert } from 'react-alert';


// TODO timer 


function LoginPage() {

    const alert = useAlert()

    const { t } = useTranslation()

    const navigate = useNavigate()

    if (!localStorage.getItem("email")) {
        navigate("/register")
    }

    const initialValues = {
        activation_password: ''
    }



    const validationSchema = Yup.object().shape({
        activation_password: Yup.string()
            .required(t('main_content.pages.auth_pages.required', "required"))
            .length(5)
    })

    const [responseError, setResponseError] = useState("")

    let smthww = t('main_content.pages.auth_pages.smthww', "something went wrong. Try again.")

    const [activateAccount, LoadingActivateAccount] = useFetching(async (values) => {
        const body = {
            "email": localStorage.getItem("email"),
            "activation_password": values.activation_password
        };

        await axios.post(`${basic_url}/users/activate_account/`, body, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
            .then(res => {
                store.dispatch({
                    type: LOGIN_SUCCESS,
                    payload: res.data,
                });
                alert.success(res.data.message)
                localStorage.removeItem("email")
                localStorage.removeItem("password")
                navigate("/")
            })
            .catch(err => {
                if (err.response) {
                    setResponseError(err.response.data.message)
                } else {
                    setResponseError(smthww)
                }

                store.dispatch({
                    type: LOGIN_FAIL,
                    payload: "Network error"
                });
            });
    })

    const [cancelingRegistration, LoadingCancelingRegistration] = useFetching(async (values) => {
        const body = {
            "email": localStorage.getItem("email"),
            "password": localStorage.getItem("password")
        };

        await axios.post(`${basic_url}/users/cancel_registration/`, body, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
            .then(res => {
                alert.success(res.data.message)

                localStorage.removeItem("email")
                localStorage.removeItem("password")
                navigate("/")
            })
            .catch(err => {
                if (err.response) {
                    setResponseError(err.response.data.message)
                } else {
                    setResponseError(smthww)
                }
                store.dispatch({
                    type: LOGIN_FAIL,
                    payload: "Network error"
                });
            });
    })

    const [time, setTimeUntil] = useState(null)

    // const runTimeUntil = (s) => {

    //     do {
    //         setTimeout(() => {
    //             setTimeUntil(Number(s))
    //             console.log(time)
    //         }, 1000)
    //     } while (time > 1)

    // }

    const [resendingActivationPassword, LoadingResendingActivationPassword] = useFetching(async (values) => {
        const body = {
            "email": localStorage.getItem("email"),
            "password": localStorage.getItem("password")
        };

        await axios.post(`${basic_url}/users/resend_activation_password/`, body, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
            .then(res => {
                alert.success(res.data.message)
                setResponseError(null)
            })
            .catch(err => {
                if (err.response) {
                    setResponseError(err.response.data.message)

                    // runTimeUntil(err.response.data.seconds_until)

                    console.log(Number(err.response.data.seconds_until))

                } else {
                    setResponseError(smthww)
                }

                store.dispatch({
                    type: LOGIN_FAIL,
                    payload: "Network error"
                });
            });
    })

    function onSubmit(values) {
        activateAccount(values)
    }

    function cancelRegistration() {
        cancelingRegistration()
    }

    function resendActivationPassword() {
        resendingActivationPassword()
    }

    let activate_account_text = t('main_content.pages.auth_pages.activate_account', "activate account")

    return (
        <>
            {(LoadingActivateAccount || LoadingCancelingRegistration || LoadingResendingActivationPassword) &&
                <div className='loader_wrapper'>
                    <Loader />
                </div>
            }

            {responseError &&
                <div className={cl.errorMessageWrapper}>
                    <span className={`${cl.errorMessage} errorMessage`}>
                        {responseError}
                    </span>
                </div>
            }

            <div className={cl.form_wrapper}>
                <Formik
                    initialValues={initialValues}
                    validationSchema={validationSchema}
                    onSubmit={onSubmit}
                >
                    {({ errors, touched, values }) => (
                        <Form
                            className={cl.form}
                        >
                            <b className={`${cl.page_name} auth_page_name`}>
                                {activate_account_text}
                            </b>

                            <FormInput
                                name="activation_password"
                                placeholder={activate_account_text}
                                type="password"
                                autoFocus={true}
                                pattern="[0-9]*"
                                inputMode="numeric"

                                value={values["activation_password"]}
                                touched={touched["activation_password"]}
                                hasError={errors["activation_password"]}
                            />

                            <div className={t.btns_wrapper}>
                                <FormButton
                                    disabled={
                                        LoadingActivateAccount ||
                                        !(isEmpty(errors))
                                    }
                                    type="submit"
                                >
                                    {activate_account_text}
                                </FormButton>

                                <span className={t.cancelRegistrationBtn} onClick={cancelRegistration}>
                                    {t('main_content.pages.auth_pages.cancel_registration', "cancel registration")}
                                </span>

                                <span className={t.cancelRegistrationBtn} onClick={resendActivationPassword}>
                                    {t('main_content.pages.auth_pages.rap', "resend activation password")}
                                </span>

                            </div>
                        </Form>
                    )}
                </Formik>
            </div>


        </>
    )
}

export default LoginPage;