import React, { useState, useEffect } from 'react'
import * as Yup from 'yup';
import { Formik, Form } from 'formik'
import { useFetching } from '../../../hooks/useFetching';
import axios from 'axios';
import { basic_url } from '../../../API/APISettings';
import FormButton from '../../../components/UI/FormButton/FormButton';
import FormInput from '../../../components/UI/FormInput/FormInput';
import { useNavigate } from 'react-router-dom';
// import { isEmpty } from '../funcs';
import cl from '../authPages.module.scss';
import Loader from '../../../components/UI/Loader/Loader';
import { useTranslation } from 'react-i18next';

import { useAlert } from 'react-alert';



function ResetPasswordPage() {

    const alert = useAlert()
    const { t } = useTranslation()
    const navigate = useNavigate()

    const [responseError, setResponseError] = useState("")

    const [buttonText, setButtonText] = useState("Send instructions")

    const [onSubmit, setOnSubmit] = useState(() => (initialValues) => sendInstructions(initialValues))

    const isDisabled = (name) => disabledInputs.includes(name) ? true : false

    const [disabledInputs, setDisabledInputs] = useState([])

    const [stateOfForm, setStateOfForm] = useState("sendActivationPassword")


    // TODO Cancel button


    const getInitialValues = () => {
        const initial_values = {
            email: '',
            reset_password: "",
            new_password: "",
            confirm_new_password: ""
        }

        try {
            let values = JSON.parse(
                localStorage.getItem("reset_password__form_values")
            )


            let initial_values_keys = Object.keys(initial_values)

            let Error = "Error"
            if (!(initial_values_keys.every(i => Object.keys(values).includes(i)))) {
                throw Error
            }

            let values_to_return = {};

            initial_values_keys.forEach(i => {
                values_to_return[i] = values[i]
            })


            return values_to_return;
        } catch {
            return initial_values
        }
    }


    const initialValues = getInitialValues()


    useEffect(() => {

        if (initialValues["email"]) {

            if (initialValues["reset_password"]) {
                setStateOfForm("setNewPassword")
            } else {
                setStateOfForm("checkResetPassword")
            }

        }

    }, [initialValues])


    const j = () => {
        switch (stateOfForm) {

            case "sendActivationPassword":
                break;
            case "checkResetPassword":
                setDisabledInputs(["email"])
                setButtonText("Check the reset password")
                setValidationSchemas({
                    ...validationSchemas,
                    reset_password: Yup.string()
                        .required(required)
                        .length(5)
                    // .test('len', 'Must be exactly 5 characters', val => val ? String(val).length === 5 : false)
                })
                setOnSubmit(() => (initialValues) => checkResetPassword(initialValues))

                break;
            case "setNewPassword":
                setDisabledInputs(["email"])
                setValidationSchemas({
                    ...validationSchemas,
                    new_password: Yup.string()
                        .required(required),
                    confirm_new_password: Yup.string()
                        .required(required)

                })
                setButtonText("Change password")
                setOnSubmit(() => (initialValues) => changePassword(initialValues))
                break;
            default:
                console.log("DEFAULT")
                break;
        }
    }





    useEffect(() => {

        j()

    }, [stateOfForm])



    let required = t('main_content.pages.auth_pages.required', "Required")


    const [validationSchemas, setValidationSchemas] = useState({
        email: Yup.string()
            .email(t('main_content.pages.auth_pages.ief', "Invalid email format"))
            .required(required),
    })


    const YupValidation = Yup.object().shape({
        ...validationSchemas
    })

    const [sendInstructions, LoadingSendInstructions] = useFetching(async (values) => {
        const body = JSON.stringify(values);

        console.log(body)

        await axios.post(`${basic_url}/users/reset_password__send_instructions/`, body, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
            .then(res => {
                alert.success(res.data.message)

                setStateOfForm("checkResetPassword")

                setResponseError("")


                localStorage.setItem("reset_password__form_values", body)


            })
            .catch(err => {
                err.response ?
                    setResponseError(err.response.data.message)
                    : setResponseError('Smth went wrong. Try again.')


            });
    })


    const [checkResetPassword, LoadingCheckResetPassword] = useFetching(async (values) => {
        const body = JSON.stringify(values);

        console.log(body)

        await axios.post(`${basic_url}/users/reset_password__check_reset_password/`, body, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
            .then(res => {
                alert.success(res.data.message)

                setStateOfForm("setNewPassword")

                setResponseError("")


                localStorage.setItem("reset_password__form_values", body)

            })
            .catch(err => {
                err.response ?
                    setResponseError(err.response.data.message)
                    : setResponseError('Smth went wrong. Try again.')
            });
    })


    const [changePassword, LoadingChangePassword] = useFetching(async (values) => {
        const body = JSON.stringify(values);

        console.log(body)

        await axios.post(`${basic_url}/users/reset_password__set_new_password/`, body, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
            .then(res => {
                alert.success(res.data.message)


                setResponseError("")

                localStorage.removeItem("reset_password__form_values")

                navigate("/users/login")

            })
            .catch(err => {
                err.response ?
                    setResponseError(err.response.data.message)
                    : setResponseError('Smth went wrong. Try again.')
            });
    })


    // LoadibgCancelRegistration
    const cancelRegistration = () => console.log("yes")


    return (
        <>
            {(LoadingSendInstructions || LoadingCheckResetPassword || LoadingChangePassword) &&
                <div className='loader_wrapper'>
                    <Loader />
                </div>
            }
            {responseError &&
                <div className={`${cl.errorMessageWrapper}`}>
                    <span className={`${cl.errorMessage} errorMessage`}>
                        {responseError}
                    </span>
                </div>
            }

            <div className={cl.form_wrapper}>
                <Formik
                    initialValues={initialValues}
                    validationSchema={YupValidation}
                    onSubmit={onSubmit}
                >
                    {({ errors, touched, values }) => (
                        <Form
                            className={`${cl.form} form`}
                            autoComplete='off'
                        >
                            <b className={`${cl.page_name} auth_page_name`}>
                                Reseting password
                            </b>

                            <FormInput
                                name="email"
                                placeholder={t('main_content.pages.auth_pages.email', "email")}
                                type="email"
                                autoFocus={true}
                                value={values["email"]}
                                touched={touched["email"]}
                                hasError={errors["email"]}
                                spellCheck="false"

                                autoComplete="true"

                                disabled={isDisabled("email")}
                            />




                            {stateOfForm === "sendActivationPassword" ?
                                <></>
                                :
                                stateOfForm === "checkResetPassword" ?
                                    <FormInput
                                        name="reset_password"
                                        placeholder="reset password"
                                        type="text"

                                        inputMode="numeric"


                                        value={values["reset_password"]}
                                        touched={touched["reset_password"]}
                                        hasError={errors["reset_password"]}
                                    />
                                    :
                                    stateOfForm === "setNewPassword" ?
                                        <>
                                            <FormInput
                                                name="new_password"
                                                placeholder="new password"
                                                type="password"

                                                value={values["new_password"]}
                                                touched={touched["new_password"]}
                                                hasError={errors["new_password"]}

                                                autoComplete="off"
                                            />

                                            <FormInput
                                                name="confirm_new_password"
                                                placeholder="confirm_new_password"
                                                type="password"

                                                value={values["confirm_new_password"]}
                                                touched={touched["confirm_new_password"]}
                                                hasError={errors["confirm_new_password"]}


                                                autoComplete="off"
                                            />
                                        </>
                                        :

                                        <span>
                                            _____ ERROR _____
                                        </span>

                            }


                            <FormButton
                                // disabled={
                                //     LoadingLoginUser ||
                                // !(isEmpty(errors) && !isEmpty(touched))
                                // }
                                type="submit"
                            >
                                {buttonText}
                            </FormButton>

                            {(stateOfForm === 'checkResetPassword' | stateOfForm === 'setNewPassword') ?

                                <div className={cl.cancelBtnWrapper}>
                                    <span className={cl.cancelBtn} onClick={cancelRegistration}>
                                        cancel
                                    </span>
                                </div>
                                : ''
                            }


                        </Form>
                    )}
                </Formik>
            </div>
        </>
    )
}

export default ResetPasswordPage;