import React, { useEffect, useState, useRef } from 'react'

import cl from '../chatPages.module.scss';

import { w3cwebsocket as W3CWebSocket } from "websocket";
import Message from '../components/Message/Message';
import MessageForm from '../components/MessageForm/MessageForm';
import ConnectedOrNotMessage from '../components/ConnectedOrNotMessage/ConnectedOrNotMessage';



function PersonalChatPage() {
    const messages_wrapper = useRef(null)

    const [message, setMessage] = useState('hello world')

    const [isUserTyping, setIsUserTyping] = useState(false)

    const [messages, setMessages] = useState({
        "Tuesday": [
            { "username": "hi", "message": "hi" },
            { "username": "some_username", "message": "hi" },
            { "username": "hi", "message": "hi" },
            { "username": "some_username", "message": "hi" },
            { "username": "hi", "message": "hi" },
            { "username": "some_username", "message": "hi" },
            { "username": "hi", "message": "hi" },
            { "username": "some_username", "message": "hi" },
        ],
        "Yesterday": [
            { "username": "hi", "message": "hi" },
            { "username": "some_username", "message": "hi" },
            { "username": "hi", "message": "hi" },
            { "username": "some_username", "message": "hi" },
            { "username": "hi", "message": "hi" },
            { "username": "some_username", "message": "hi" },
            { "username": "hi", "message": "hi" },
            { "username": "some_username", "message": "hi\n  So, What is up?\n hello world\n" },
        ],
        "Today": [
            { "username": "some_username", "message": "hi" },
            { "username": "hi", "message": "hi" },
            { "username": "hi", "message": "\nhello world\n     ,    ,         )   \n" },
            { "username": "some_username", "message": "hi" },
            { "username": "some_username", "message": "hello" },
            { "username": "hi", "message": "hi" },
            { "username": "some_username", "message": "hi" },
        ]
    })

    const [connected, setConnected] = useState(null)

    const roomname = "hello"

    const username = 'hi'

    const chatSocket = new W3CWebSocket(
        `ws://${"192.168.0.106:8000"}/ws/chat/${roomname}/`
    )


    chatSocket.close()


    document.getElementsByTagName("body")[0].style.overflow = 'hidden'

    const tryToOpenConnectionUntilSuccess = (connected, setConnected) => {
        console.log("trying to connect")

        const t = setInterval(() => {
            chatSocket.onopen = () => {
                console.log("connected")
                setConnected(true)
            }

            // TODO
            // ! connected -> null
            // console.log(connected)
            if (connected) {
                console.log("interval cleared")
                clearInterval(t)
            }
        }, 1000)
    }


    // useEffect(() => {
    //     console.log("useEffect on connected")
    //     tryToOpenConnectionUntilSuccess(connected, setConnected)
    // }, [connected])


    useEffect(() => {
        // tryToOpenConnectionUntilSuccess(connected, setConnected)

        return () => {
            document.getElementsByTagName("body")[0].style.overflow = "auto";

            chatSocket.close()
        }
    }, [])

    chatSocket.onclose = () => {
        console.log("closed connection")
        setConnected(false)
    }

    chatSocket.onmessage = (message) => {
        console.log("onmessage")
        const dataFromServer = JSON.parse(message.data)
        if (dataFromServer) {
            console.log(dataFromServer)

            const updatedMessages = [...messages, dataFromServer]
            setMessages(updatedMessages)

            if (username === dataFromServer.username) {
                let messages_wrapper_current = messages_wrapper.current
                messages_wrapper_current.scrollTop = messages_wrapper_current.scrollHeight
            }

        }
    }

    useEffect(() => {

        if (isUserTyping) {
            // TODO
            console.log("typing")
        }

    }, [isUserTyping])

    const onSubmit = (e) => {
        e.preventDefault()

        console.log("onSubmit")

        chatSocket.send(JSON.stringify({
            username: "alan",
            message: message,
        }))

        // TODO

        // ! TO delete
        let m = Object.assign({}, messages)
        m['Today'] = [...m['Today'],
        { "username": username, "message": message }
        ]

        setMessages(m)


        let messages_wrapper_current = messages_wrapper.current
        messages_wrapper_current.scrollTop = messages_wrapper_current.scrollHeight

        setMessage('')
    }


    return (
        <div className={cl.main_wrapper}>
            <div className={cl.main_content_wrapper}>
                <div className={cl.main_content}>
                    <div className={cl.main}>

                        <div className={cl.top_wrapper}>
                            <ConnectedOrNotMessage connected={connected} />
                        </div>

                        <div
                            className={cl.messages_wrapper}
                            ref={messages_wrapper}
                        >

                            <div className={cl.messages_content}>
                                {messages ?

                                    Object.keys(messages).map((date, index) =>

                                        <div
                                            key={index}
                                            className={cl.messages_date_group}
                                        >
                                            <div className={cl.sticky_date_wrapper}>
                                                <div className={cl.sticky_date}>
                                                    {date}
                                                </div>
                                            </div>

                                            {messages[date].map((m, index) =>
                                                <Message
                                                    key={index}
                                                    data={m}
                                                    isOutgoing={m.username === username}
                                                />
                                            )}

                                        </div>
                                    )

                                    :
                                    <div className={cl.no_messages_yet_text_wrapper}>
                                        <span className={cl.no_messages_yet_text}>
                                            No messages yet.
                                        </span>
                                    </div>
                                }

                            </div>

                        </div>

                        <div className={cl.messageFormWrapper}>
                            <MessageForm
                                message={message}
                                setMessage={setMessage}
                                onSubmit={onSubmit}
                                setIsUserTyping={setIsUserTyping}
                            />
                        </div>

                    </div>

                </div>

            </div>
        </div>
    )
}

export default PersonalChatPage