import React, { useEffect, useState } from 'react'
import * as Yup from 'yup';
import { Formik, Form } from 'formik'
import { useFetching } from '../../../hooks/useFetching';
import axios from 'axios';
import { basic_url, reCaptchaKey } from '../../../API/APISettings';
import FormButton from '../../../components/UI/FormButton/FormButton';
import FormInput from '../../../components/UI/FormInput/FormInput';
import { Link, useNavigate } from 'react-router-dom';
import { isEmpty } from '../funcs';
import cl from '../authPages.module.scss';
import Loader from '../../../components/UI/Loader/Loader';
import { useTranslation } from 'react-i18next';

import ReCAPTCHA from "react-google-recaptcha";

import { HashLink } from 'react-router-hash-link';



function RegisterPage() {

  const { t } = useTranslation()

  const navigate = useNavigate()

  function redirectIfCurrentRegistrationContinues() {
    let c = Boolean(localStorage.getItem('email') && localStorage.getItem('password'))

    if (c) {
      navigate("/users/activate-account")
    }
  }

  useEffect(() => {
    redirectIfCurrentRegistrationContinues()
  })

  const initialValues = {
    email: 'metal@m.com',
    username: 'James',
    first_name: 'James',
    last_name: 'Hetfield',
    password: 'metallica',
    rePassword: 'metallica',
  }

  const [captchaLoading, setCaptchaLoading] = useState(false)

  const [captchaResult, setCaptchaResult] = useState()

  const [captchaValue, setCaptchaValue] = useState('')

  const handleRecaptcha = (value) => {
    setCaptchaLoading(true)

    let body = JSON.stringify({ 'captcha_value': value })

    axios.post(`${basic_url}/users/recaptcha/`, body, {
      headers: {
        'Content-Type': 'application/json'
      }
    })
      .then(res => {
        setCaptchaResult(res.data.captcha)
        setCaptchaValue(value)
      })
      .catch((err) => console.log(err))
      .finally(() => setCaptchaLoading(false))
  }


  let required = t('main_content.pages.auth_pages.required', "Required")
  let pmcalxc = t('main_content.pages.auth_pages.pmcalxc', "Password must contain at least 8 characters")
  let pmm = t('main_content.pages.auth_pages.pmm', "Passwords must match")

  const validationSchema = Yup.object(
    {
      email: Yup.string()
        .email(
          t('main_content.pages.auth_pages.ief', "Invalid email format")
        )
        .required(required),
      username: Yup.string()
        .min(2)
        .required(required),
      first_name: Yup.string()
        .min(2)
        .required(required),
      last_name: Yup.string(),
      password: Yup.string()
        .required(required)
        .min(8, pmcalxc)
        .oneOf([Yup.ref('rePassword'), null], pmm),
      rePassword: Yup.string()
        .oneOf([Yup.ref('password'), null], pmm)
        .min(8, pmcalxc)
        .required(required)
    })

  const [responseErrors, setResponseErrors] = useState(["Hello",])

  const [registerUser, LoadingRegisterUser] = useFetching(async (values) => {
    const body = JSON.stringify({ ...values, captcha_value: captchaValue });

    await axios.post(`${basic_url}/users/auth/register/`, body, {
      headers: {
        'Content-Type': 'application/json'
      }
    })
      .then(res => {
        localStorage.setItem("email", res.data.user.email)
        localStorage.setItem("password", res.data.user.password)

        redirectIfCurrentRegistrationContinues()
      })
      .catch(err => {

        if (err.response) {
          setResponseErrors(err.response.data.messages)
        } else {
          setResponseErrors(['Smth went wrong. Try again.'])
        }
        window.scrollTo(0, 0)

        window.grecaptcha.reset()

        setCaptchaResult(false)
        setCaptchaValue()

        console.log(captchaValue)
      });
  })

  function onSubmit(values) {
    registerUser(values)
  }

  // TODO Recaptcha loader

  return (
    <>
      {LoadingRegisterUser &&
        <div className='loader_wrapper'>
          <Loader />
        </div>
      }
      {responseErrors &&
        <div className={cl.errorMessageWrapper}>
          {responseErrors.map(val =>
            <span key={val} className={`${cl.errorMessage} errorMessage`}>
              {val}
            </span>
          )}

          <br />

          <span>
            <HashLink
              className={`${cl.help_link} help_link`}
              to='#hello'
              scroll={(el) => el.scrollIntoView({ behavior: 'auto', block: 'center' })}
            >
              {t('main_content.pages.auth_pages.dynh', "Do you need help?")}
            </HashLink>
          </span>
        </div>
      }



      <div className={cl.form_wrapper}>
        <Formik
          initialValues={initialValues}
          validationSchema={validationSchema}
          onSubmit={onSubmit}
        >
          {({ errors, touched, values }) => (
            <Form
              className={cl.form}
              autocomplete="off"
              spellCheck="false"
            >
              <b className={`${cl.page_name} auth_page_name`}>
                {t('main_content.pages.auth_pages.register', "register")}
              </b>

              <FormInput
                name="email"
                placeholder={t('main_content.pages.auth_pages.email', "email")}
                type="email"
                autoFocus={true}
                tabIndex="1"
                value={values["email"]}
                touched={touched["email"]}
                hasError={errors["email"]}
              />

              <FormInput
                name="username"
                placeholder={t('main_content.pages.auth_pages.username', "username")}
                tabIndex="2"
                value={values["username"]}
                touched={touched["username"]}
                hasError={errors["username"]}
              />

              <FormInput
                name="first_name"
                placeholder={t('main_content.pages.auth_pages.first_name', "first name")}
                tabIndex="3"
                value={values["first_name"]}
                touched={touched["first_name"]}
                hasError={errors["first_name"]}

                spellCheck="false"
              />

              <FormInput
                name="last_name"
                placeholder={`${t('main_content.pages.auth_pages.last_name', "last name")}*`}
                tabIndex="4"
                value={values["last_name"]}
                touched={touched["last_name"]}
                hasError={errors["last_name"]}

                spellCheck="false"
              />

              <FormInput
                type="password"
                name="password"
                placeholder={t('main_content.pages.auth_pages.password', "password")}
                tabIndex="5"
                value={values["password"]}
                touched={touched["password"]}
                hasError={errors["password"]}
              />

              <FormInput
                type="password"
                name="rePassword"
                placeholder={t('main_content.pages.auth_pages.re_password', "re-password")}
                tabIndex="6"
                value={values["rePassword"]}
                touched={touched["rePassword"]}
                hasError={errors["rePassword"]}
              />

              <div className={cl.reCaptcha_wrapper}>
                <ReCAPTCHA
                  sitekey={reCaptchaKey}
                  onChange={handleRecaptcha}
                />
              </div>

              {captchaLoading &&
                <span className={cl.captcha_loading}>
                  <span className={cl.captcha_loading_message}>
                    loading
                  </span>
                </span>
              }

              {(captchaResult & !captchaLoading) ?
                <FormButton
                  disabled={
                    LoadingRegisterUser ||
                    !(isEmpty(errors) && !isEmpty(touched))
                  }
                  type="submit"
                  tabIndex="7"
                >
                  {t('main_content.pages.auth_pages.sign_up', "sign up")}
                </FormButton>
                : <></>
              }



              <div className={cl.lw}>
                <span
                  className={cl.ttl}
                >
                  {t('main_content.pages.auth_pages.aha', "Already have an account?")}
                </span>
                <br />
                <Link
                  className={`${cl.link} link_to_another_auth_page`}
                  to="/users/login"
                >
                  {t('main_content.pages.auth_pages.login', "login")}
                </Link>
              </div>

            </Form>
          )}
        </Formik>
      </div>

      {responseErrors &&
        <div className={cl.help_wrapper} id="hello">
          If you faced problems with registration, there're some instructions for you.
          <br />
          Cases:

          Lorem ipsum dolor sit amet consectetur adipisicing elit. Quisquam veritatis sunt provident, autem voluptate corporis eaque commodi voluptatibus architecto doloribus odio tenetur aliquam consequuntur, alias ab quam quod amet. Ea, laudantium blanditiis quam corrupti magni suscipit recusandae. Doloremque eos dolorem iure repellat enim repellendus numquam, quis, veritatis totam, nam autem animi obcaecati eveniet fugiat ea. Dicta doloremque voluptates iste quos delectus dolorem, magni harum praesentium laudantium illum suscipit magnam corporis sapiente animi debitis recusandae est consequatur fuga. Minima assumenda accusantium delectus eius doloremque nihil animi labore fugit. Quo molestiae beatae iure recusandae sit, necessitatibus officiis voluptatibus eius officia voluptate mollitia!
          <ul>
            <li>

            </li>
          </ul>

        </div>
      }

    </>
  )
}

export default RegisterPage;